import { PrefIO } from './prefIO'
import tinycolor2 from 'tinycolor2'

export enum ColorKey {
  Number,
  Trigger,
  Side
}

export type ColorPref = { [key in ColorKey]: tinycolor2.ColorFormats.RGBA }

export function defaultColorPref (): ColorPref {
  return {
    [ColorKey.Number]: { r: 204, g: 204, b: 204, a: 1 },
    [ColorKey.Trigger]: { r: 254, g: 146, b: 0, a: 1 },
    [ColorKey.Side]: { r: 164, g: 221, b: 0, a: 1 }
  }
}

export const colorPrefIO = new PrefIO('kColorPref', defaultColorPref)
