import { merge } from 'lodash-es'
import { Ref, ref, watch } from 'vue'

const ioKeySet = new Set<string>()

export class PrefIO <T> {
  prefKey: string
  pref: Ref<T>
  defaultConstructor: () => T

  constructor (
    prefKey: string,
    defaultConstructor: () => T
  ) {
    this.prefKey = prefKey
    this.defaultConstructor = defaultConstructor
    this.pref = ref(this._load()) as Ref<T>
    watch(this.pref, (v) => {
      localStorage.setItem(this.prefKey, JSON.stringify(v))
    }, { deep: true })

    if (ioKeySet.has(prefKey)) {
      throw new Error('Duplicate prefKey')
    }
    ioKeySet.add(prefKey)
  }

  _load (): T {
    const pref = localStorage.getItem(this.prefKey)
    const defaultValue = this.defaultConstructor.call(null)
    if (pref) {
      return merge(defaultValue, JSON.parse(pref))
    } else return defaultValue
  }

  get (): T {
    return this.pref.value
  }

  clear () {
    this.pref.value = this.defaultConstructor.call(null)
    localStorage.removeItem(this.prefKey)
  }

  set (newPref: T) {
    this.pref.value = newPref
    localStorage.setItem(this.prefKey, JSON.stringify(this.pref.value))
  }
}
