import { onBeforeUnmount, onMounted, Ref } from 'vue'

export function crossPlatformRequestVideoFrameCallback (videoEl: HTMLVideoElement, cb: () => void) {
  if ((videoEl as any).requestVideoFrameCallback) {
    return (videoEl as any).requestVideoFrameCallback(cb)
  } else {
    return requestAnimationFrame(cb)
  }
}

export function crossPlatformCancelVideoFrameCallback (videoEl: HTMLVideoElement, reqId: number) {
  if ((videoEl as any).cancelVideoFrameCallback) {
    return (videoEl as any).cancelVideoFrameCallback(reqId)
  } else {
    return cancelAnimationFrame(reqId)
  }
}

export function useVideoFrameCallback (videoEl: Ref<HTMLVideoElement | null>, onVideoFrame: () => void) {
// render overlay canvas
  let videoIntervalHandle: null | number = null
  onMounted(() => {
    if (videoEl.value) {
      videoIntervalHandle = crossPlatformRequestVideoFrameCallback(videoEl.value, cb)
    }
  })

  onBeforeUnmount(() => {
    if (videoIntervalHandle) {
      if (videoEl.value) {
        crossPlatformCancelVideoFrameCallback(videoEl.value, videoIntervalHandle)
      }
      videoIntervalHandle = null
    }
  })

  function cb () {
    if (videoEl.value) {
      videoIntervalHandle = crossPlatformRequestVideoFrameCallback(videoEl.value, cb)
      onVideoFrame()
    }
  }
}
