import { Ref, ref } from 'vue'

export function useBookmarkList (
  props: {
    currentTime: Ref<number>,
    getAudioDuration: () => number
  }
) {
  const bookmarkList = ref([] as number[])

  function gotoPrevBookmark () {
    if (bookmarkList.value.length === 0) {
      props.currentTime.value = 0
      return
    }

    for (let i = 0; i < bookmarkList.value.length; i++) {
      if (bookmarkList.value[i] > props.currentTime.value - 1) {
        if (i === 0) props.currentTime.value = 0
        else props.currentTime.value = bookmarkList.value[i - 1]
        return
      }
    }

    // goto last bookmark.
    props.currentTime.value = bookmarkList.value[bookmarkList.value.length - 1]
  }

  function gotoNextBookmark () {
    if (bookmarkList.value.length === 0) {
      props.currentTime.value = props.getAudioDuration.call(null)
      return
    }

    for (let i = bookmarkList.value.length - 1; i >= 0; i--) {
      if (bookmarkList.value[i] < props.currentTime.value) {
        if (i === bookmarkList.value.length - 1) props.currentTime.value = props.getAudioDuration.call(null)
        else props.currentTime.value = bookmarkList.value[i + 1]
        return
      }
    }

    props.currentTime.value = bookmarkList.value[0]
  }

  function toggleBookmark () {
    if (bookmarkList.value.length === 0) {
      bookmarkList.value.push(props.currentTime.value)
      return
    }

    const searchMin = props.currentTime.value - 1.5
    const searchMax = props.currentTime.value + 1.5
    const index = bookmarkList.value.findIndex(x => x <= searchMax && x >= searchMin)
    if (index !== -1) {
      bookmarkList.value.splice(index, 1)
      return
    }

    bookmarkList.value.push(props.currentTime.value)
    bookmarkList.value.sort((a, b) => a - b)
  }

  function clearBookmarks () {
    bookmarkList.value.length = 0
  }

  return {
    bookmarkList,
    gotoPrevBookmark,
    gotoNextBookmark,
    toggleBookmark,
    clearBookmarks
  }
}
