import { onMounted, onBeforeUnmount } from 'vue'

/**
 * Register key handlers.
 *
 * Unfortunately keydown / keyup handler cannot be separated...
 *  - keydown/keyup handler are NOT guaranteed to be paired 1-1.
 *
 * @param handlers: keyDown / keyUp Handler
 */
export function useKeyHandler (handlers: {
  keyDown?: (key: string, ev: KeyboardEvent) => boolean,
  keyUp?: (key: string, ev: KeyboardEvent) => boolean,
}) {
  const { keyDown, keyUp } = handlers

  onMounted(() => {
    document.addEventListener('keydown', keydownListener)
    document.addEventListener('keyup', keyupListener)
  })

  onBeforeUnmount(() => {
    document.removeEventListener('keydown', keydownListener)
    document.removeEventListener('keyup', keyupListener)
  })

  const lastRetCache = {} as { [key: string]: boolean }

  function keydownListener (ev: KeyboardEvent) {
    const keyString = `${ev.key}_${ev.location}`
    let ret = lastRetCache[keyString]
    if (ret === undefined) {
      if (keyDown) ret = keyDown(keyString.toLowerCase(), ev) || false
      else ret = false
    }
    lastRetCache[keyString] = ret

    if (ret) {
      ev.stopPropagation()
      ev.preventDefault()
    }
  }

  function keyupListener (ev: KeyboardEvent) {
    const keyString = `${ev.key}_${ev.location}`

    if (lastRetCache[keyString] === undefined) return
    delete lastRetCache[keyString]

    if (keyUp) {
      const ret = keyUp(keyString.toLowerCase(), ev)
      if (ret) {
        ev.stopPropagation()
        ev.preventDefault()
      }
    }
  }
}
