import { VideoProps } from '@/pref/VideoProps'
import { djmaxLanes4b, djmaxLanes5b, djmaxLanes6b, djmaxLanes8b } from './djmaxLanes'
import { GameEnvName, GearPos } from './list'
import tinycolor2 from 'tinycolor2'

export interface NotePos {
  x: number
  y: number
  w: number
}

export interface Lane {
  name: string
  pos(props: VideoProps, videoTime: number, noteTime: number): NotePos
  color(): tinycolor2.ColorFormats.RGBA
}

export interface GameEnv {
  name: GameEnvName
  judgementLinePos: { x: number, y: number }
  playAreaWidth: number
  lanes: Lane[]
}

export function getGameEnv (props: {
  gameEnvName: GameEnvName,
  gearPos: GearPos
}) {
  switch (props.gameEnvName) {
    case 'djmax_4b': return djmaxLanes4b(props.gearPos)
    case 'djmax_5b': return djmaxLanes5b(props.gearPos)
    case 'djmax_6b': return djmaxLanes6b(props.gearPos)
    case 'djmax_8b': return djmaxLanes8b(props.gearPos)
  }
}
