export type GameEnvName = (
  'djmax_4b' |
  'djmax_5b' |
  'djmax_6b' |
  'djmax_8b'
)

export type GearPos = (
  'left' |
  'center' |
  'right'
)

export function checkGameEnvName (name: string): GameEnvName | null {
  switch (name) {
    case 'djmax_4b':
    case 'djmax_5b':
    case 'djmax_6b':
    case 'djmax_8b':
      return name
    default:
      return null
  }
}
