export function getDevicePixelRatio () {
  return window.devicePixelRatio || 1
}

export function resizeCanvasDPIAware (canvas: HTMLCanvasElement, size: { width: number, height: number}) {
  // Get the device pixel ratio, falling back to 1.
  const dpr = getDevicePixelRatio()
  // Get the size of the canvas in CSS pixels.
  // Give the canvas pixel dimensions of their CSS
  // size * the device pixel ratio.
  const properW = (size.width * dpr) | 0
  const properH = (size.height * dpr) | 0
  if (canvas.width !== properW || canvas.height !== properH) {
    canvas.width = properW
    canvas.height = properH

    const ctx = canvas.getContext('2d')!
    ctx.resetTransform()
    ctx.scale(dpr, dpr)
  }
}
