import { checkGameEnvName, GameEnvName, GearPos } from '@/gameEnv/list'
import { merge } from 'lodash-es'

export interface VideoProps {
  noteSpeedMultiplier: number
  gameEnvName: GameEnvName
  gearPos: GearPos
  audioSync: number
}

export function createDefaultVideoProps (gameEnvName_: string): VideoProps {
  const gameEnvName = checkGameEnvName(gameEnvName_)
  if (!gameEnvName) throw new Error(`Invalid env name ${gameEnvName}`)

  return {
    noteSpeedMultiplier: 5.4,
    gearPos: 'center',
    gameEnvName,
    audioSync: 0
  }
}

export function loadFileVideoProps (filename: string): {
  props: VideoProps,
  isNew?: boolean
} {
  const s = localStorage.getItem('videoProps_' + filename)
  if (s) {
    const obj = JSON.parse(s) as VideoProps
    try {
      return {
        props: merge(
          createDefaultVideoProps(obj.gameEnvName),
          obj
        )
      }
    } catch (e) {
      console.error('Error loading previous videoprops, resetting...', e)
    }
  }

  const keyCount = Number(prompt('Key Count') || '6')
  const envName = `djmax_${keyCount}b`
  return {
    props: createDefaultVideoProps(envName),
    isNew: true
  }
}
