import { GearPos } from './list'
import { ColorKey, colorPrefIO } from '@/pref/ColorPref'
import { VideoProps } from '@/pref/VideoProps'
import { Lane, GameEnv } from '.'

const playAreaWidth = 0.26042

const judgementLinePosY = 0.6963
function judgementLinePosX (pos: GearPos): number {
  switch (pos) {
    case 'left': return 0.037
    case 'center': return 0.369
    case 'right': return 0.701
  }
}

function calcY (videoProps: VideoProps, videoTime: number, noteTime: number) {
  const yPerSecond = 0.25648 * videoProps.noteSpeedMultiplier
  return judgementLinePosY - yPerSecond * (noteTime - videoTime)
}

function createNumberLane (index1: number, keyCount: number): Lane {
  let noteSizeW = playAreaWidth
  switch (keyCount) {
    case 4: noteSizeW /= 4; break
    case 5: noteSizeW /= 5; break
    case 6: case 8: noteSizeW /= 6; break
    default: throw new Error('Bad keyCount')
  }

  return {
    name: `Lane${index1}`,
    pos (videoProps, videoTime, noteTime) {
      return {
        x: noteSizeW * (index1 - 1) + judgementLinePosX(videoProps.gearPos),
        y: calcY(videoProps, videoTime, noteTime),
        w: noteSizeW
      }
    },
    color () {
      return colorPrefIO.get()[ColorKey.Number]
    }
  }
}

function createSideLane (direction: string, index0: number): Lane {
  return {
    name: `Side${direction}`,
    pos (videoProps, videoTime, noteTime) {
      return {
        x: playAreaWidth / 2 * index0 + judgementLinePosX(videoProps.gearPos),
        y: calcY(videoProps, videoTime, noteTime),
        w: playAreaWidth / 2
      }
    },
    color () {
      return colorPrefIO.get()[ColorKey.Side]
    }
  }
}

function createTriggerLane (direction: string, index0: number): Lane {
  return {
    name: `Trigger${direction}`,
    pos (videoProps, videoTime, noteTime) {
      return {
        x: playAreaWidth / 2 * index0 + judgementLinePosX(videoProps.gearPos),
        y: calcY(videoProps, videoTime, noteTime),
        w: playAreaWidth / 2
      }
    },
    color () {
      return colorPrefIO.get()[ColorKey.Trigger]
    }
  }
}

/// ///////////

export function djmaxLanes8b (gearPos: GearPos): GameEnv {
  return {
    name: 'djmax_8b',
    playAreaWidth,
    judgementLinePos: { x: judgementLinePosX(gearPos), y: judgementLinePosY },
    lanes: [
      createNumberLane(1, 8),
      createNumberLane(2, 8),
      createNumberLane(3, 8),
      createNumberLane(4, 8),
      createNumberLane(5, 8),
      createNumberLane(6, 8),

      createSideLane('L', 0),
      createSideLane('R', 1),

      createTriggerLane('L', 0),
      createTriggerLane('R', 1)
    ]
  }
}

