import { audioLatencyIO } from '@/pref/AudioLatency'
import { VideoProps } from '@/pref/VideoProps'
import { KeyLog } from '@/KeyLog'
import tinycolor from 'tinycolor2'
import { toRaw } from 'vue'
import { getGameEnv } from '@/gameEnv'

export function updateNoteOverlayCanvas (
  videoElement: HTMLVideoElement,
  targetCanvas: HTMLCanvasElement,
  videoProps_: VideoProps,
  keyLog_: KeyLog
) {
  const videoProps = toRaw(videoProps_)
  const keyLog = toRaw(keyLog_)

  const ctx = targetCanvas.getContext('2d')!
  const { width, height } = targetCanvas
  const currentTime = videoElement.currentTime + videoProps.audioSync + audioLatencyIO.get()
  const gameEnv = getGameEnv(videoProps)
  const { judgementLinePos } = gameEnv

  const yPerSecond = 0.25648 * videoProps.noteSpeedMultiplier
  const perfectRange = 0.042
  const perfectY = perfectRange * yPerSecond

  ctx.clearRect(0, 0, width, height)

  // Enter scaled world
  const oldTransform = ctx.getTransform()
  ctx.setTransform(1, 0, 0, 1, 0, 0)
  ctx.scale(width, height)

  // darken non-note-regions.
  ctx.fillStyle = '#000000e0'
  ctx.rect(0, 0, judgementLinePos.x, 1)
  ctx.rect(judgementLinePos.x + gameEnv.playAreaWidth, 0, 1, 1)
  ctx.fill()

  // Draw note region boundary
  ctx.strokeStyle = 'blue'
  ctx.lineWidth = 0.001
  ctx.beginPath()

  ctx.moveTo(judgementLinePos.x, 0)
  ctx.lineTo(judgementLinePos.x, 1)

  ctx.moveTo(judgementLinePos.x + gameEnv.playAreaWidth, 0)
  ctx.lineTo(judgementLinePos.x + gameEnv.playAreaWidth, 1)

  ctx.moveTo(judgementLinePos.x, judgementLinePos.y)
  ctx.lineTo(judgementLinePos.x + gameEnv.playAreaWidth, judgementLinePos.y)

  ctx.stroke()

  // render notes
  for (const keyEntry of keyLog) {
    // Calculate Y coord of start/end

    const { lane } = keyEntry
    const keydownY = lane.pos(videoProps, currentTime, keyEntry.keydownTime).y
    const keyupY = lane.pos(videoProps, currentTime, keyEntry.keyupTime || currentTime).y

    // Don't render invisible notes
    if (keydownY < -perfectY || keyupY > 1 + perfectY) continue

    // Color
    const keydownPos = lane.pos(videoProps, currentTime, keyEntry.keydownTime)
    const noteColorString = lane.color()
    const noteColorRGB = tinycolor(noteColorString).toRgb()
    const { x: rectX, w: rectW } = keydownPos

    // Render
    ctx.fillStyle = `rgba(${noteColorRGB.r}, ${noteColorRGB.g}, ${noteColorRGB.b}, 0.8)`
    ctx.beginPath()
    ctx.moveTo(rectX + rectW / 2, keydownY - perfectY / 2)
    ctx.lineTo(rectX, keydownY)
    ctx.lineTo(rectX + rectW / 2, keydownY + perfectY / 2)
    ctx.lineTo(rectX + rectW, keydownY)
    ctx.closePath()
    ctx.fill()

    // Render keyup
    const trailW = rectW * 0.12
    ctx.fillStyle = `rgba(${noteColorRGB.r}, ${noteColorRGB.g}, ${noteColorRGB.b}, 0.2)`
    ctx.beginPath()
    ctx.rect(rectX + rectW / 2 - trailW / 2, keyupY, trailW, (keydownY - keyupY))
    ctx.fill()

    // Render perfect range
    // ctx.beginPath()
    // ctx.strokeStyle = '#ffffffc0'
    // ctx.lineWidth = 0.004
    // ctx.moveTo(rectX + rectW / 2, keydownY - perfectY + rectH / 2)
    // ctx.lineTo(rectX + rectW / 2, keydownY + perfectY + rectH / 2)
    // ctx.stroke()
  }
  // revert old transform
  ctx.setTransform(oldTransform)
}
