import { Lane } from '@/gameEnv'
import { Ref, ref } from 'vue'
import { KeyLog } from '../KeyLog'

export function useKeylog (props: { getCurrentTime: () => number }) {
  const keyLog = ref([]) as Ref<KeyLog>

  function clearKeyLog () {
    keyLog.value.length = 0
  }

  function addKeyDown (lane: Lane) {
    const accurateCurrentTime = props.getCurrentTime.call(null)

    keyLog.value.push({
      timestamp: Date.now(),
      lane,
      keydownTime: accurateCurrentTime,
      keyupTime: null
    })
  }

  function addKeyUp (lane: Lane) {
    const accurateCurrentTime = props.getCurrentTime.call(null)
    const currentTimestamp = Date.now()
    for (let i = keyLog.value.length - 1; i >= 0; i--) {
      const logEntry = keyLog.value[i]
      if (logEntry.timestamp >= currentTimestamp) break

      // Note: due to vue's reactivity thing, logEntry.lane may be a proxy to original lane object
      // so comparing lane object itself may lead to comparing proxy to original object (false)
      // We use === on primitive type (typeof lane.name === "string")
      if (logEntry.lane.name === lane.name) {
        if (logEntry.keyupTime === null) {
          logEntry.keyupTime = accurateCurrentTime
          return true
        }
        break
      }
    }
    return false
  }

  function closeAllKeylog () {
    for (const keyEntry of keyLog.value) {
      if (keyEntry.keyupTime === null) {
        keyEntry.keyupTime = keyEntry.keydownTime
      }
    }
  }

  return {
    keyLog,
    clearKeyLog,
    addKeyDown,
    addKeyUp,
    closeAllKeylog
  }
}
